html {
  font-family: 'Noto Sans JP',
    sans-serif;
  font-weight: 700;
  color: #503c14;
  touch-action: manipulation;
}

body {
  background-color: #ffffff;
  width: 100vw;
  margin: 0;
}

#header {
  text-align: center;
  font-size: 4vh;
}

#main {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

#main button {
  font-family: 'Noto Sans JP',
    sans-serif;
  font-weight: 700;
  color: #503c14;
  font-size: 130%;
  border-radius: 3px;
  border: none;
  background-color: #dfdfdf;
  transition: background-color 0.2s;
}

#main button:hover {
  background-color: #cfcfcf;
}

#main button:active {
  background-color: #b8b8b8;
}


#footer {
  position: fixed;
  width: 100%;
  bottom: 0;
}

a {
  color: #503c14;
  text-decoration: none;
}

#counter {
  color: #6AAA64;
}

.cash {
  color: #503c14;
  text-decoration: none;
  position: absolute;
  font-size: 2vh;
  left: calc(50vw - 14vh);
  bottom: 1px;
}

#start {
  margin: 0 10px 0 0;
}

.tweetButtonImage {
  margin: 0;
  width: 4vh;
  position: relative;
  bottom: -1vh;
}